.App {
  min-height: 100vh;
}

/** To make an HTML table naturally wrap its text **/
.table-text-wrap {
  th,
  td { 
    max-width: 150px; /* Set a maximum width for cells */
    overflow: hidden; /* Hide overflow content */
    white-space: wrap; 
  }
}
/** To make an HTML table naturally truncate its text **/
.table-text-truncate {
  th,
  td { 
    max-width: 150px; /* Set a maximum width for cells */
    overflow: hidden; /* Hide overflow content */
    white-space: nowrap; /* Prevent text wrapping */
    text-overflow: ellipsis; /* Show ellipsis for overflowing text */
  }
}
/** To make an HTML table limit the width of it's first elements of each row **/
.table-definition {
  th,
  td { 
    &:nth-of-type(1) {
      width: 70px;
    }
  }
}


.accordion-footer {
  display: flex;
  gap: 1rem;
}


.btn {
  text-decoration: none!important;
}
.btn-secondary { 
  background-color: #dbdbdb;
  border: 1px solid #b5b5b5;
  color: #313131!important;

  &:hover {
    background-color: #adadad;
  }
}
.btn-icon {
  width: fit-content;
  display: flex; 
  align-items: center;
  gap: .5rem;
}


.btn-link {
  color: #941e89;
  &:hover {
    text-decoration: none;
  }
}

.main-sidebar {
  position: fixed;
  height: 100vh;
  padding: 3.75rem 2.4rem;
  overflow-y: scroll;
  // color: #fff;

  &-header {
    margin-bottom: 1.875rem;

    .logo {
      max-width: 44px;
    }
  }

  &-heading {
    color: var(--website-col-aaa-1c);
    text-transform: uppercase;
    font-size: 1.125rem;
  }

  .menu-list {
    padding-left: 0;
    list-style: none;

    a {
      display: block;
      position: relative;
      text-decoration: none;
      color: var(--website-col-aaa-1c);
      padding: 0.4rem 1rem 0.4rem 1.75rem;

      &:hover {
        color: var(--website-col-bbb-1);
        background-color: rgba(250, 250, 250, 0.1);

        &::before {
          background-color: var(--website-col-bbb-1);
        }
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0.875rem;
        width: 3px;
        height: 100%;
        background-color: rgba(250, 250, 250, 0.4);
      }

      &.active {
        color: var(--website-col-bbb-2);
        background-color: rgba(250, 250, 250, 0.1);

        &::before {
          background-color: var(--website-col-bbb-2);
        }
      }
    }

    ul {
      list-style: none;
      padding-left: 0.875rem;

      a {
        padding: 0.15rem 1rem 0.15rem 1.75rem;
        font-size: 0.95rem;

        &::before {
          left: 0;
        }
      }
    }
  }
}

.chakra-breadcrumb {
  margin-bottom: 1.25rem;

  .chakra-breadcrumb__list {
    margin: 0;
    padding: 0;
    color: var(--website-col-aaa-2);

    a {
      color: var(--website-col-aaa-2);
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }
  font-weight: bold;
}

.main-content {
  padding: 3.5rem 3.75rem;
}
.main-content a {
  text-decoration: underline;
}
.main-content a:hover {
  text-decoration: none;
}

.form-feedback textarea {
